import React, { useState, useRef } from "react";
import { Link } from "gatsby";
import PropTypes from "prop-types"
import Sticky from 'react-sticky-el'
import Popup from './shared/popup'

import logo from '../images/nav/logo.svg';
import logoWhite from '../images/nav/logo-white.svg';
import pages from '../constants/pages';

const Header = (props) => {
  const [isNavbarOpen, setNavbarOpen] = useState(false);
  const [showTelephonePopover, setShowTelephonePopover] = useState(false);
  const target = useRef(null);

  return (
    <header>
      <div className='mx-lg-5'>
        <TopNavigation
          isNavbarOpen={isNavbarOpen}
        />

        <Sticky style={{ zIndex: 5 }}>
          <MainNavigation
            isNavbarOpen={isNavbarOpen}
            setNavbarOpen={(v) => setNavbarOpen(v)}
            showTelephonePopover={showTelephonePopover}
            setShowTelephonePopover={(v) => setShowTelephonePopover(v)}
            target={target}
          />
        </Sticky>
      </div>
    </header>
  )
}

const MainNavigation = ({ isNavbarOpen, setNavbarOpen, showTelephonePopover, setShowTelephonePopover, target }) => {
  return (
    <nav className={"navbar navbar-dark navbar-expand-lg justify-content-between main-navbar " + (isNavbarOpen && "open")}>
      <div className="container-fluid mx-0 mx-lg-5 ">
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target=".navbar-collapse"
          onClick={(_) => {
            setNavbarOpen(!isNavbarOpen);
          }}
        >
          {isNavbarOpen ? (
            <span className="icon navbar-toggler-close-icon"></span>
          ) : (
              <span className="navbar-toggler-icon"></span>
            )}
        </button>

        <Logo
          isNavbarOpen={isNavbarOpen}
          logo={logo}
          logoWhite={logoWhite}
        />
        <div className='d-lg-none d-block'>
          <button className='navbar-toggler' ref={target} onClick={(_) => setShowTelephonePopover(!showTelephonePopover)}>
            <span className="icon telephone-toggler-icon"></span>
          </button>
          <Popup
            showTelephonePopover={showTelephonePopover}
            target={target}
          >
            <div className='popup d-flex justify-content-end align-items-end py-4 pl-4 pr-3'>
              <div className='text-right justify-content-between'>
                <div className='my-2'>
                  <p>Текущи клиенти:</p>
                  <h2 className='number-blue'><a href="tel:0885250000">088 525 0000</a></h2>
                </div>
                <div className='my-2'>
                  <p>Включи се</p>
                  <h2 className='number-green'><a href="tel:0885250000">088 299 1611</a></h2>
                </div>
              </div>
            </div>
          </Popup>
        </div>
        <div className='d-xl-block d-none order-2'>
          <Link to={"/somewhere"} className='btn btn-primary green'>Виж пакетите</Link>
        </div>
        <div className={"navbar-collapse collapse w-100 order-2 order-md-0"}>
          <ul className="navbar-nav ml-auto">
            {pages.links.map(({ name, href, active }, index) => (
              <li key={index} className="nav-item mt-2 mt-lg-3 mx-3">
                <Link to={href} className={"nav-link d-inline-block " + (active && " active")}>{name}</Link>
                {active && (
                  <div className="nav-link-active"></div>
                )}
              </li>
            ))}
            <li className="nav-item my-4 mt-lg-3 mx-3">
              {isNavbarOpen && (<button className='btn btn-primary green'>Включи се</button>)}
            </li>
          </ul>
        </div>
      </div>
    </nav>
  )
}

const TopNavigation = ({ isNavbarOpen }) => {
  const target = useRef(null);
  const [show, setShow] = useState(false);

  return (
    <div className={'top-nav ' + (isNavbarOpen && "open")}>
      <div className='row mx-lg-5 h-100'>
        <div className='col-4 d-none d-lg-flex justify-content-start align-items-center pl-lg-5 h-100 top-left-section'>
        </div>
        <div className='col-lg-3 d-none d-lg-block'></div>
        <div className={'col-12 col-lg-5 d-flex align-self-center ' + (isNavbarOpen ? "justify-content-start" : "justify-content-end")}>
          <div className="d-inline px-3">
            <Link to={"/coverage"}>Покритие</Link>
          </div>
          <div className="topnav-item d-inline px-3">
            <Link to={"/shops"}>Магазини</Link>
          </div>
          <div className="topnav-item pl-2 d-inline" ref={target} onClick={(_) => { setShow(!show); }}>
            <span className='icon phone-icon d-none d-lg-inline-block'></span> <a className='mx-1 d-none d-lg-inline-block'>Позвъни</a>
          </div>
          <Popup
            showTelephonePopover={show}
            setShowTelephonePopover={(v) => setShow(v)}
            target={target}
          >
            <div className='popup d-flex justify-content-end align-items-end py-4 pl-4 pr-3'>
              <div className='text-right justify-content-between'>
                <div className='my-2'>
                  <p>Текущи клиенти:</p>
                  <h2 className='number-blue'><a href="tel:0885250000">088 525 0000</a></h2>
                </div>
                <div className='my-2'>
                  <p>Включи се</p>
                  <h2 className='number-green'><a href="tel:0885250000">088 299 1611</a></h2>
                </div>
              </div>
            </div>
          </Popup>
          <div className="topnav-item d-inline pl-2">
            <span className='icon log-in-icon d-none d-lg-inline-block'></span><a className='mx-1'>Вход</a>
          </div>
        </div>
      </div>
    </div>
  )
}

const Logo = ({ isNavbarOpen, logoWhite, logo }) => (
  <Link to={"/"} className={"navbar-brand mx-lg-5 d-block text-center order-0 order-md-0 "}>
    <img src={isNavbarOpen ? logoWhite : logo} className="img-fluid logo" alt="Logo" />
  </Link>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
