import React from 'react';
import { Overlay } from "react-bootstrap";
// import PropTypes from 'prop-types';

const Popup = ({ showTelephonePopover, target, children, placement }) => (
  <Overlay
    target={target.current}
    show={showTelephonePopover}
    placement={placement}
    rootClose={true}
    rootCloseEvent={'click'}
  >
    {({
      placement,
      scheduleUpdate,
      arrowProps,
      outOfBoundaries,
      show: _show,
      ...props
    }) => (
        <div {...props}>
          {children}
        </div>
      )}
  </Overlay>
)


Popup.defaultProps = {
  placement: 'bottom'
}

export default Popup;